@import "./src/scss/_modules/variables";

table.dataTable > tbody > tr.child ul.dtr-details { width: 100% }

.sorting_asc,
.sorting_desc, 
.even .sorting_1 {
	background-color: rgba($primary-500, 0.03);
}

.odd .sorting_1 {
	background-color: rgba($primary-500, 0.04);
}

.table-dark {
	.sorting_asc,
	.sorting_desc, 
	.even .sorting_1 {
		background-color: rgba($warning-500, 0.15);
	}

	.odd .sorting_1 {
		background-color: rgba($warning-500, 0.15);
	}
}

/* some idiot put "important" rule on the main css file now I have to put important here to override it... eghhh */
/* I had to set it to 0 because auto fill cannot "compute" for the margin ... nice job Allan >.> */
table.dataTable {
	margin-top: 0 !important;
	margin-bottom: 1rem !important;
}


table.dataTable.dtr-column > tbody > tr > td.control, 
table.dataTable.dtr-column > tbody > tr > th.control {
	padding-left: 30px;
}

table.dataTable.dtr-inline.collapsed > tbody > tr[role="row"] > td:first-child:before, 
table.dataTable.dtr-inline.collapsed > tbody > tr[role="row"] > th:first-child:before,
table.dataTable.dtr-column > tbody > tr > td.control:before, 
table.dataTable.dtr-column > tbody > tr > th.control:before {

	top: 0;
	bottom: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	height:100%;
	width: 1rem;
	box-shadow: none;
	border-radius: 0;
	border: 0;
	margin: 0;
	background-color: $primary-500;
}

table.dataTable.dtr-inline.collapsed > tbody > tr.parent > td:first-child:before, 
table.dataTable.dtr-inline.collapsed > tbody > tr.parent > th:first-child:before,
table.dataTable.dtr-column > tbody > tr.parent td.control:before, 
table.dataTable.dtr-column > tbody > tr.parent th.control:before {
	background-color: $success-500;
}

table.dataTable thead .sorting:before,
table.dataTable thead .sorting_asc:before,
table.dataTable thead .sorting_desc:before,
table.dataTable thead .sorting_asc_disabled:before,
table.dataTable thead .sorting_desc_disabled:before {
	right: 1rem !important;
	content: "\f175";
	font-size: 14px;
	font-family: 'Font Awesome 5 Pro';
}
table.dataTable thead .sorting:after,
table.dataTable thead .sorting_asc:after,
table.dataTable thead .sorting_desc:after,
table.dataTable thead .sorting_asc_disabled:after,
table.dataTable thead .sorting_desc_disabled:after {
	right: 0.5rem !important;
	content: "\f176";
	font-size: 14px;
	font-family: 'Font Awesome 5 Pro';
}

.dataTables_empty {
	color: $danger-500;
	font-size: 2rem;
	padding-top: 2.5rem !important;
	padding-bottom: 2.5rem !important;
}

.dataTables_wrapper .dataTables_filter label {
	display: inline-flex;
}

.dataTables_wrapper tr.child td.child {
	padding: 0;

	.dtr-details {
		padding: 1rem 1rem 1.5rem 1.95rem;
		position: relative;

		&:before {
			content: "\f149";
			font-family: 'Font Awesome 5 Pro';
			color: $success-400;
			width: 20px;
			height: 30px;
			position: absolute;
			top: -15px;
			right: 0px;
			font-size: 1.2rem;
		}

		&:after {
			content: "";
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			width: 1rem;
			background: $success-200;

		}
	}

}

