}

/* FixedColumns */
.DTFC_LeftHeadWrapper,
.DTFC_LeftBodyWrapper,
.DTFC_LeftFootWrapper {
	
	&:before {
		content:"";
		position:absolute;
		right:0px;
		top:1px;
		bottom:0px;
		width:1px;
		background: $danger-500;
		z-index: 1;
    	box-shadow: -4px 0px 4px rgba($black,0.4);
	}

	table.table-bordered {
		border-right-width: 0;
	}

}

/* KeyTable */
table.dataTable tbody th.focus, 
table.dataTable tbody td.focus {
    box-shadow: inset 0 0 0px 1px $primary-500;
    background: rgba($primary-500, 0.1);
    font-weight: 500;
}

/* RowGroup */
table.dataTable tr.dtrg-group td {
	background: transparent;
	font-size: 1rem;
    font-weight: 400;	
    padding-top: 1.25rem;
}

table.dataTable.table-sm tr.dtrg-group td {
	padding-top: 1rem;
}

table.dataTable:not(.table-dark) tr.dtrg-group td {
	background: $white;
}

/* FixedHeader */
table.dataTable.fixedHeader-floating {
	opacity: 0.9;
}

/* RowReorder */
table.dataTable td.reorder {
	text-align: inherit;
}

tr.dt-rowReorder-moving {
  outline: 2px solid $success-500;
  outline-offset: -2px; }

table.dt-rowReorder-float {
  outline: 2px solid $primary-500;
  outline-offset: -2px; }  


/* w-100 bug for some tables */
.DTCR_clonedTable.w-100,
.dt-rowReorder-float.w-100,
.fixedHeader-floating.w-100 {
	width: inherit !important;
}

/* fix z-index for processing message */
.dataTables_processing {
	z-index: 2;
}

/* Select */
table.dataTable  {

	&.table-bordered {
		.selected {
			td {
				border-color: rgba($black,0.1);
			}
		}
		td.selected {
			border-color: rgba($black,0.1);
		}
	}

	tbody {
		> tr.selected, 
		> tr > .selected {
			box-shadow: inset 0 0 0px 1px $primary-500;
			background: rgba($primary-500, 0.1);
			font-weight: 500;
			color: inherit;
		}
	}

		
}


